<template>
  <!--  自动轮播到最后一页就不会自动轮播()从网络上获取-->
  <!--  只需要在swiper组件上添加v-if="banners.length > 0-->
  <swiper :options="swiperOption" class="banner" v-if="banners.length > 0">
    <swiper-slide v-for="value in banners" :key="value.bannerID" class="item">
      <a href="">
        <img :src="value.bannerImg" alt="">
      </a>
    </swiper-slide>
    <div class="swiper-pagination" slot="pagination"></div>
  </swiper>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
  name: 'Banner',
  data () {
    return {
      swiperOption: {
        loop: true,
        autoplay: {
          delay: 1000,
          stopOnLastSlide: false,
          disableOnInteraction: false
        },
        pagination: {
          el: '.swiper-pagination'
        },
        observer: true,
        observeParents: true,
        observeSlideChildren: true
      }
    }
  },
  props: {
    banners: {
      type: Array,
      default: () => [],
      required: true
    }
  },
  components: {
    swiper,
    swiperSlide
  }
}
</script>
<!--scoped是局部设置-->
<style scoped lang="scss">
.banner{
  .item{
    img{
      width: 100%;
      height: 300px;
    }
  }
}
</style>
<!--如果要覆盖swiper样式不能使用scoped-->
<!--样式没有立刻生效可能存在缓存问题-->
<style lang="scss">
@import "../../assets/css/mixin";
.banner{
  .swiper-pagination-bullet{
    width: 16px;
    height: 16px;
    background: white;
    opacity: 1;
  }
  .swiper-pagination-bullet-active{
    @include bg_color();
  }
}
</style>
